import React, { useEffect } from 'react';
import ReactHtmlParser from 'react-html-parser';
import { useMediaQuery } from 'react-responsive';
import { useSelector, useDispatch } from 'react-redux';
import moment from 'moment';
import {
    Link,
    Switch,
    Route,
    useRouteMatch,
    useParams,
    useHistory
} from 'react-router-dom';
import {
    fetchList,
    changeKeyword
} from '../../feature/blog/blogSlice';
import Post from './Post';
import ListItem from './ListItem';
import Footer from '../Footer';
import empty from '../../images/empty.png';
import { url } from '../../json/domain.json';

const BlogContent = () => {
    const dispatch = useDispatch();
    const history = useHistory();
    const { path, url } = useRouteMatch();
    const list = useSelector(state => state.blog.list);
    const article = useSelector(state => state.blog.selected);
    const keyword = useSelector(state => state.blog.keyword);
    const isDesktop = useMediaQuery({ query: '(min-width: 1005px)' });
    const handleChangeKeyword = e => {
        dispatch(changeKeyword(e.target.value));
        if (!e.target.value) {
            history.push(`${url}`);
        }
    }
    const handleSearch = () => {
        if (keyword) {
            history.push(`/blog/type/search`);
        }
    }
    useEffect(() => {
        if (!list) dispatch(fetchList());
        if (article) history.push(`/blog/detail/${article.id}`);
    }, [list, article, history, dispatch])
    return (
        <>
        <nav className='blog-nav'>
            <Link to={`${url}`}>首頁</Link>
            <Link to={`${url}/type/recap`}>聚會回顧</Link>
            <Link to={`${url}/type/reflect`}>契友心得</Link>
        </nav>
        <Switch>
            <Route exact path={`${path}`}>
                <main className='blog-content'>
                    <div className='blog-search-content'>
                        <input className='blog-search-bar' type='text' placeholder='搜尋關鍵字' value={keyword} onChange={handleChangeKeyword} />
                        <button type='button' className='blog-search-button' onClick={handleSearch}>搜尋</button>
                    </div>
                    {!list && <div className='loading'>載入中...</div>}
                    {list && list.filter(i => i.tab_category === '聚會回顧').length > 0 && <div>
                        <div className='blog-category-title'>
                            <Link to={`${url}/type/recap`}>聚會回顧</Link>
                        </div>
                        <div className={isDesktop ? 'recap-container' : ''}>
                        {
                            list.filter(i => i.tab_category === '聚會回顧').map((rp, index) => {
                                if (index === 0) return <Post key={index} index={index} {...rp} />
                                return <ListItem key={index} index={index} {...rp} />
                            })
                        }
                        </div>
                    </div>}
                    {list && list.filter(i => i.tab_category === '契友心得').length > 0 && <div>
                        <div className='blog-category-title'>
                            <Link to={`${url}/type/reflect`}>契友心得</Link>
                        </div>
                        <div className={isDesktop ? 'recap-container' : ''}>
                        {
                            list.filter(i => i.tab_category === '契友心得').map((rp, index) => {
                                if (index === 0) return <Post key={index} index={index} {...rp} />
                                return <ListItem key={index} index={index} {...rp} />
                            })
                        }
                        </div>
                    </div>}
                    <Footer />
                </main>
            </Route>
            <Route path={`${path}/type/:topicId`}>
                <ContentType keyword={keyword} handleChangeKeyword={handleChangeKeyword} />
            </Route>
            <Route path={`${path}/detail/:blogId`}>
                <BlogDetail />
            </Route>
        </Switch>
        </>
    );
}
const ContentType = ({ keyword, handleChangeKeyword }) => {
    const { topicId } = useParams();
    const list = useSelector(state => state.blog.list);
    let targetList = null;
    switch (topicId) {
        case 'recap':
            targetList = list.filter(i => i.tab_category === '聚會回顧');
            break;
        case 'reflect':
            targetList = list.filter(i => i.tab_category === '契友心得');
            break;
        case 'search':
            targetList = list.filter(item => item.title.includes(keyword) || item.descp?.includes(keyword));
            break;
        default:
            targetList = [];
    }
    return (
        <main className='blog-content'>
            <div className='blog-search-content'>
                <input className='blog-search-bar' type='text' placeholder='搜尋關鍵字' value={keyword} onChange={handleChangeKeyword} />
                <button type='button' className='blog-search-button'>搜尋</button>
            </div>
            {
                targetList.length > 0 ? 
                targetList.map(target => <Post key={target.title} {...target} />) : 
                <div className='loading'>此分類查無文章</div>
            }
            <Footer />
        </main>
    )
}
const BlogDetail = () => {
    const article = useSelector(state => state.blog.selected);
    return (
        <main className='blog-content'>
            <div className='blog-detail'>
                <img alt='preview' src={`${article?.banner ? url + article.banner.url : empty}`} />
                <div className='title'>{article?.title || ''}</div>
                <div className='time'>{article?.updated_at ? moment(article.updated_at).format('YYYY/MM/DD') : ''}</div>
                <div>
                    <div className='type'>{article?.tab_category || ''}</div>
                </div>
                <p className='source'>{ReactHtmlParser(article?.descrip || '')}</p>
                <p>{ReactHtmlParser(article?.contact || '')}</p>
            </div>
            <Footer />
        </main>
    )
}

export default BlogContent;