import React from 'react';
import moment from 'moment';
import { useMediaQuery } from 'react-responsive';
import { useDispatch } from 'react-redux';
import { fetchItem } from '../../feature/blog/blogSlice';
import { url } from '../../json/domain.json';
import empty from '../../images/empty.png';

const ListItem = ({ index, id, banner, title, updated_at, desc }) => {
    const isDesktop = useMediaQuery({ query: '(min-width: 1005px)' });
    const dispatch = useDispatch();
    const handleClick = () => { dispatch(fetchItem(id)); }
    return (
        <div className={`blog-list-item blog-list-item-${index} pointer${isDesktop ? '' : ' space'}`} onClick={handleClick}>
            <img alt='item-preview' src={banner ? url + banner.url : empty} />
            <div className='blog-list-item-detail'>
                <h3>{title}</h3>
                <label>{moment(updated_at).format('YYYY/MM/DD')}</label>
                <p>{desc || ''}</p>
            </div>
        </div>
    )
}

export default ListItem;