import React from 'react';
import moment from 'moment';
import { useDispatch } from 'react-redux';
import { fetchItem } from '../../feature/blog/blogSlice';
import { url } from '../../json/domain.json';
import empty from '../../images/empty.png';

const Post = props => {
    const { index, id, title, updated_at, descrip, banner } = props;
    const dispatch = useDispatch();
    const handleClick = () => { dispatch(fetchItem(id)); }
    return (
        <div className={index === 0 ? `post-item post-item-${index} pointer` : 'post-item pointer'} style={{backgroundImage:`url('${banner ? url + banner.url : empty}`}} onClick={handleClick}>
            <div className='post-detail'>
                <h3>{title}</h3>
                <label>{moment(updated_at).format('YYYY/MM/DD')}</label>
                <p>{descrip || ''}</p>
            </div>
        </div>
    )
}

export default Post;