import React, { useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { useMediaQuery } from 'react-responsive';

const Navigation = () => {
    const history = useHistory();
    const [isDropped, setDrop] = useState(false);
    const isDesktop = useMediaQuery({ query: '(min-width: 1005px)' });
    const handleDropDown = () => setDrop(!isDropped);
    const handleScrollTop = () => window.scrollTo({ top: 0, left: 0, behavior: 'smooth' });
    const handleScroll = e => {
        const id = e.target.id;
        if (window.location.pathname !== '/') history.push('/');
        setTimeout(() => { // 執行非同步動作
            window.scrollTo({
                top: document.querySelector(id).offsetTop,
                left: 0,
                behavior: 'smooth'
            });
            !isDesktop && setDrop(false);
        }, 0);
    }
    return (
        <nav className='navigation'>
            <div>
                <div className='nav-logo'>
                    <img className='pointer' alt='nav-logo' height='48' src='/images/etech_logo_w.png' onClick={handleScrollTop} />
                    <Link to='/' onClick={handleScrollTop} rel='noreferrer'>享Tech團契</Link>
                </div>
                {isDesktop && <ul className='menu-list'>
                    <li id='#contact' onClick={handleScroll}>聯絡資訊</li>
                    <li>
                        <Link to='/blog' rel='noreferrer'>部落格</Link>
                    </li>
                    <li id='#news' onClick={handleScroll}>最新消息</li>
                    <li id='#about' onClick={handleScroll}>關於享Tech</li>
                </ul>}
                {!isDesktop && <div className='menu-btn'>
                    <img alt='menu' src='/images/btn.png' height='40' onClick={handleDropDown} />
                </div>}
                {!isDesktop && <ul className={`menu-dropdown-list${isDropped ? ' drop' : ''}`}>
                    <li id='#about' onClick={handleScroll}>關於享Tech</li>
                    <li id='#news' onClick={handleScroll}>最新消息</li>
                    <li>
                        <Link to='/blog' rel='noreferrer'>部落格</Link>
                    </li>
                    <li id='#contact' onClick={handleScroll}>聯絡資訊</li>
                </ul>}
            </div>
        </nav>
    );
}

export default Navigation;