import React from 'react';
import {
  BrowserRouter,
  Switch,
  Route
} from 'react-router-dom';
import Home from './pages/Home';
import Blog from './pages/Blog';

const App = () => (
  <BrowserRouter>
    <Switch>
      <Route exact path='/'>
        <Home />
      </Route>
      <Route path='/blog'>
        <Blog />
      </Route>
    </Switch>
  </BrowserRouter>
);

export default App;
